@use 'vars' as *;

// Home page ----------------------------------------------------------------------------------
.home-page {
  background:var(--background-home);

    h3{

      margin-bottom:0;
      color: #ffffff;
    }
    .button-group {
      display: flex;
      justify-content: center;
      gap: 1rem; 
      width: 100%;
     
      sl-button::part(base) {
          color: var(--base-txt-color);
          background-color: #ffffff;
          font-size: 1rem; 
          padding: 1rem 4rem 1rem; 
          width: 100%; 
          border-radius: 12px;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
      }
    }  
}


  ///------home-banner--------
  .home-banner {
    background: url('../../static/images/home/home-bg-graphics.svg');
    background-size: 100%;
    background-position: top;
    background-repeat: no-repeat;
    background-attachment: fixed;
    margin: 5rem auto 0;
    position: relative;
    color: white;
    display: flex;
    flex-direction: column; 
    justify-content: flex-start; 
    align-items: center; 
    overflow: hidden;
  
    img {
      margin: -5rem 0 2rem 0;
      position: relative;
      z-index: 1;
    }
  
    h1 {
        color: #6D83F2;
        font-size: 2.5rem; // Adjust size to be responsive 
        margin-right: 10rem;
        text-align: left;
        z-index: 2;
  }
  
    h2 {
      color: #F2DE96;
      font-size: 1.5rem; 
      padding: 5px;
      margin: 0; 
      text-align: center; 
      z-index: 2; 
    }
  
    .blurb-container {
        position: relative;
        display: center;
  }
}

  // animations home banner-------https://codepen.io/MarioDesigns/pen/woJgeo-----
  
 .cloud {
  animation: float 5s ease-in-out infinite;
}

@keyframes float {
  0% {
   
    transform: translateY(0px);
  }
  50% {
  
    transform: translateY(-20px);
  }
  100% {
   
    transform: translateY(0px);
  }
}
  // ---------Responsive design-----------------

  @media (max-width: 768px) { // Tablets & smaller screens
   
  .home-banner {

    background-size: contain; // Ensure the background image fits the container
    background-attachment: scroll; // Fix scrolling issues on mobile
   
    h1 {
      font-size: 2.5rem;
      margin-right: 7rem;
    }
   
    img{
     margin:-4rem 0 0rem 0;
    }
    
      
  }
  }


  @media (min-width: 768px) and (max-width: 991px) {
    .home-page {
      .home-banner {
        h1 {
          font-size: 2.5rem;
          margin-right: 10rem;
        }
      }
  
      .button-group {
        display: flex;
        justify-content: center;
        gap: 1rem; 
        width: 100%;
       
        sl-button::part(base) {
            color: var(--base-txt-color);
            background-color: #ffffff;
            font-size: 1rem; 
            padding: 1rem 2rem 1rem; 
            width: 100%; 
            border-radius: 12px;
            box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
        }
    }
  }
}
  @media (max-width: 480px) {
    .home-page {
    .home-banner {
          background-size: 31rem;
          background-position: top center;
        h1 {
          font-size: 1.8rem;
          margin-right: 5rem;
        }
      }
    }  
  }
